.appbar {
    background-color: white !important;
    box-shadow: none !important;
    z-index: 1;
    padding: 0.5rem 3rem;
}

.logo {
    height: 4rem;
    width: auto;
}

.nav-link {
    color: var(--color-primary) !important;
    border-bottom: 1px solid white;
}

.nav-link:hover {
    color: var(--color-light) !important;
}

.nav-active {
    border-bottom: 1px solid var(--color-dark);
}