.full-height {
    height: calc(100vh - 5rem);
    display: flex;
    align-items: center;
}

.hero-img {
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
    
}
.hero-caption {
    color: var(--color-primary);
}
.hero-caption .title {
    text-align: right;
    font-size: 3rem;
}
.hero-caption .text {
    text-align: justify;
    font-size: 1rem;
}

/* ----- Media Queries (tablets) ----- */
@media screen and (max-width: 1024px) {
    .full-height {
        height: 100%;
    }
    .hero-caption {
        padding: 3rem 0;
    }
}

/* ----- Media Queries (phones) ----- */
@media screen and (max-width: 600px) {
    .full-height {
        height: 100%;
    }
    .hero-caption {
        padding: 3rem 0;
    }
}