#about {
    background-color: #EFEFEF;
    padding: 6rem 0;
}

.grid-container {
    position: relative;
}

.left-grid, .right-grid {
    display: flex;
    flex-direction: column !important;
    justify-content: center;
}

.left-grid > img, .right-grid > img {
    max-height: 25rem;
    object-fit: contain;
    padding: 1rem;
}

.about-text {
    display: block;
    text-align: justify;
    margin: 1rem 0 !important;
    color: var(--color-primary);
}

.middle-line {
    border-left: 2px solid var(--color-primary);
    height: 100%;
    position: absolute;
    left: 50%;
    margin-left: -1px;
    top: 0;
}

.tl-icon {
    position: absolute;
    top: 40%;
    right: 48%;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    filter: drop-shadow(1px 2px 3px var(--color-dark));
}